.btn--secondary::after,
.btn::after {
	top: .5rem;
	left: .5rem;
	z-index: -1;
	height: 100%;
	width: 100%;
	content: ""
}

[role=button],
button[type=button] {
	cursor: pointer;
	// font-family: $primary-font, sans-serif;
}

[role=button].disabled,
button[type=button].disabled {
	cursor: default
}

.btn,
.btn-icon,
.modal__trigger,
.select select:hover {
	cursor: pointer
}

.btn {
	letter-spacing: .1em;
	padding: 1.125em 3.75em 1em;
	background: #ed3fa8;
	border: 1px solid transparent;
	border-radius: 0;
	color: #fff;
	font-size: .875em;
	font-weight: 700;
	-webkit-transition: font-size .4s ease-in-out, padding .4s ease-in-out;
	transition: font-size .4s ease-in-out, padding .4s ease-in-out;
	position: relative;
}
