.btn.disabled,
.btn:disabled {
	opacity: .5
}

.btn::after {
	position: absolute;
	border: 1px solid #ed3fa8;
}

.btn--secondary {
	background: #fff;
	color: #ed3fa8;
	border: 1px solid #ed3fa8;
	letter-spacing: 0;
}

.btn--secondary::after {
	position: absolute;
	background-color: #ed3fa8;
	border: 1px solid #fff;
}

.btn--tertiary {
	background: Transparent;
	color: #ed3fa8;
	border: 1px solid #ed3fa8;
	padding-left: .9375rem;
	padding-right: .9375rem
}

.btn--tertiary::after {
	display: none
}

.btn--sign-up {
	position: absolute;
	top: 1.0625rem;
	right: -.9375rem;
	font-size: .8125rem;
	padding: 1rem 1.4375rem
}

.btn.is-loading,
.form-inline__input-container,
.select {
	position: relative
}

.btn--full {
	width: 100%
}

.btn__label {
	display: inline-block;
	-webkit-transition: opacity .2s ease-in-out, -webkit-transform .2s ease-in-out;
	transition: opacity .2s ease-in-out, -webkit-transform .2s ease-in-out;
	transition: opacity .2s ease-in-out, transform .2s ease-in-out;
	transition: opacity .2s ease-in-out, transform .2s ease-in-out, -webkit-transform .2s ease-in-out
}

.btn.is-loading::before {
	font-size: .5625em
}

.btn.is-loading .btn__label {
	opacity: 0;
	-webkit-transform: scale(.9);
	transform: scale(.9)
}

.btn-link {
	background: 0 0;
	border: 0;
	border-bottom: 1px solid #646464;
	color: #646464;
	padding: 0
}

.btn-icon,
fieldset {
	border: 0;
	padding: 0
}

.btn-icon {
	background: 0 0
}

.form-list {
	display: -webkit-box;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	flex-wrap: wrap;
	font-size: 1em;
	-webkit-box-pack: justify;
	-webkit-justify-content: space-between;
	justify-content: space-between;
	list-style: none;
	margin: 0;
	padding: 0
}

.form-item {
	margin-bottom: 1.125em;
	padding-bottom: 0;
	width: 100%;

	&:last-of-type {
		margin-bottom: rem(20);
