.form-inline {
	display: -webkit-box;
	display: -webkit-flex;
	display: flex
}

.form-inline__input-cta {
	margin-bottom: .75rem;
	text-align: center
}

.form-inline__input {
	-webkit-box-flex: 1;
	-webkit-flex: 1 0 0%;
	flex: 1 0 0%
}
