.page-wrap {
	-webkit-transition: all .4s ease-in-out, margin .4s ease-in-out, -webkit-transform .4s ease-in-out;
	transition: all .4s ease-in-out, margin .4s ease-in-out, -webkit-transform .4s ease-in-out;
	transition: all .4s ease-in-out, transform .4s ease-in-out, margin .4s ease-in-out;
	transition: all .4s ease-in-out, transform .4s ease-in-out, margin .4s ease-in-out, -webkit-transform .4s ease-in-out;
	padding-top: 0
}

.container {
	margin: 0 auto;
	max-width: 70.625rem;
	padding-left: 1.3125em;
	padding-right: 1.3125em;
	-webkit-transition: padding .4s ease-in-out;
	transition: padding .4s ease-in-out
}

.hdr,
.hdr__icon {
	-webkit-transition: all .2s ease-in-out
}


.page-wrap {
	-webkit-box-flex: 1;
	-webkit-flex: 1 0 auto;
	flex: 1 0 auto
}

.footer {
	background-color: #fefaff;
	background-size: 9.375rem
}

.footer__wrap {
	padding-top: 2.6875em
}

.footer .input-placeholder .input,
.footer .input-placeholder label {
	font-size: .8125rem;
	padding: 1rem 1.125rem
}

.footer-col-wrap {
	display: flex
}

.hdr__list,
.hdr__wrap,
.hdr__wrap>div {
	display: -webkit-box;
	display: -webkit-flex
}

.footer-social {
	padding: 2.5em 0
}

.footer-nl-cta {
	margin-bottom: 1.14286rem;
	font-size: .875rem
}

.footer-copyright {
	margin: 3.25rem 0 .75rem;
	font-weight: 500;
	font-size: .625rem;
	color: #909090
}

.hdr__count,
.hdr__link {
	color: #8b8b8b;
	text-transform: uppercase;
	font-weight: 500;
	letter-spacing: .15em
}

.hdr {
	top: 0;
	left: 0;
	right: 0;
	z-index: 13;
	transition: all .2s ease-in-out
}

.hdr__icon {
	position: absolute;
	transition: all .2s ease-in-out;
	-webkit-transform: translate3d(-50%, -50%, 0);
	transform: translate3d(-50%, -50%, 0);
	top: 50%;
	left: 50%
}

.hdr__icon--pink {
	opacity: 0;
	margin: .125rem 0 0 .0625rem;
	z-index: 1
}

.hdr__icon--white {
	opacity: 0;
	z-index: 2
}

.hdr__icon--pink-nonfixed {
	opacity: 1;
	z-index: 0
}

.hdr__cart-icon {
	-webkit-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out;
	position: absolute;
	top: 0;
	left: 0
}

.hdr__link,
.hdr__list li {
	-webkit-transition: all .1s ease-in-out
}

.hdr__cart-white {
	opacity: 0
}

.hdr__cart-pink,
.white-theme .hdr__cart-white {
	opacity: 1
}

.hdr__wrap {
	display: flex;
	-webkit-box-align: center;
	-webkit-align-items: center;
	align-items: center;
	-webkit-box-pack: center;
	-webkit-justify-content: center;
	justify-content: center;
	height: 4.125em
}

.hdr__list,
.hdr__wrap>div {
	display: flex;
	-webkit-box-align: center;
	-webkit-align-items: center;
	align-items: center
}

.hdr__cart,
.hdr__logo-link {
	display: -webkit-box;
	display: -webkit-flex
}

.hdr__list li {
	padding: 0;
	margin-left: 2.375em;
	transition: all .1s ease-in-out
}

.hdr__list li:first-child,
.hdr__right li:first-child {
	margin-left: 0
}

.hdr__logo {
	-webkit-box-pack: center;
	-webkit-justify-content: center;
	justify-content: center
}

.hdr__logo-wrap {
	position: relative;
	width: 100%;
	height: 0;
	overflow: hidden;
	padding-bottom: 19.18239%
}

.hdr__logo-link {
	display: flex;
	-webkit-box-align: center;
	-webkit-align-items: center;
	align-items: center;
	-webkit-box-pack: center;
	-webkit-justify-content: center;
	justify-content: center
}

.hdr__right {
	-webkit-box-pack: end;
	-webkit-justify-content: flex-end;
	justify-content: flex-end
}

.hdr-promo,
.hdr__cart {
	-webkit-box-align: center
}

.hdr__right li {
	padding: 0
}

.hdr__link {
	font-size: .875em;
	border-bottom: 1px solid transparent;
	transition: all .1s ease-in-out
}

.hdr__link:hover {
	border-bottom-color: #ed3fa8
}

.hdr__cart {
	display: flex;
	-webkit-align-items: center;
	align-items: center;
	position: relative
}

.hdr__cart-btn {
	position: absolute;
	top: -.5em
}

.hdr__count {
	font-size: .75em;
	line-height: 1;
	margin-left: 2.33333em;
	-webkit-transition: all .1s ease-in-out;
	transition: all .1s ease-in-out
}

.white-theme .hdr__count,
.white-theme .hdr__link {
	color: #fff
}

.white-theme .hdr__cart-pink {
	opacity: 0
}

.white-theme .hdr__icon--pink {
	z-index: 1;
	opacity: .3
}

.white-theme .hdr__icon--white {
	opacity: 1;
	z-index: 2
}

.white-theme .hdr__icon--pink-nonfixed {
	opacity: 0;
	z-index: 3
}

.hdr-promo {
	background-position: 50% 0;
	height: 1.875rem;
	display: -webkit-box;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	align-items: center;
	-webkit-box-pack: center;
	-webkit-justify-content: center;
	justify-content: center
}

.hdr-promo__copy {
	font-size: .8125em;
	color: #fff;
	text-transform: uppercase;
	font-weight: 500;
	letter-spacing: .15em;
	line-height: 1
}

.lazyload,
.lazyloading {
	opacity: 0;
	-webkit-transition: opacity .4s ease-in-out;
	transition: opacity .4s ease-in-out
}

.lazyloaded {
	opacity: 1;
	-webkit-transition: opacity .3s;
	transition: opacity .3s
}

.is-load-container {
	position: relative
}

.is-load-container::after {
	background: #fff;
	bottom: 0;
	content: ' ';
	left: 0;
	opacity: 1;
	position: absolute;
	right: 0;
	top: 0;
	-webkit-transition: all .4s ease-in-out;
	transition: all .4s ease-in-out;
	visibility: visible;
	z-index: 2
}

.modal,
.newsletter__btn.is-loading span {
	visibility: hidden
}

.is-load-container--loaded::after {
	opacity: 0;
	visibility: hidden
}

@-webkit-keyframes rotating {
	from {
		-webkit-transform: rotate(0);
		transform: rotate(0)
	}
	to {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg)
	}
}

@keyframes rotating {
	from {
		-webkit-transform: rotate(0);
		transform: rotate(0)
	}
	to {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg)
	}
}

.is-loading {
	position: relative
}

.newsletter__btn,
.newsletter__btn.is-loading,
.sweep-top-bg {
	position: absolute
}

.is-loading::before {
	content: ' '
}

.is-loading--pink::before {
	background-image: url(loading-pink.svg)
}

.newsletter__messages.is-visible {
	height: auto!important;
	padding-bottom: .8125em!important;
	padding-top: .8125em!important;
	text-align: center
}

.newsletter__btn {
	bottom: -.58333em;
	display: inline-block;
	height: 3.33333em;
	font-size: .75em;
	margin: 0;
	right: -.58333em;
	z-index: 2
}

.sweep p,
.sweep-ul {
	font-size: .875rem;
	line-height: 1.6
}

.newsletter__label {
	z-index: 3
}

body {
	background-color: #FEF9FF;
}

.sweep {
	position: relative;
}

.sweep-top-bg {
	background-repeat: no-repeat;
	background-position: left top;
	background-size: 100%;
	left: 0;
	top: 0;
	bottom: 0;
	max-width: 53.375rem;
	width: 61%;
	z-index: -1
}

.sweep-ul {
	margin-bottom: 1.25rem;
	margin-top: -.625rem
}

.sweep-ul li {
	padding-bottom: 0
}

.sweep a {
	text-decoration: underline
}

.sweep h1 {
	font-size: 3.25rem;
	font-weight: 700;
	line-height: 1;
	margin: .625rem 0 1.875rem
}

.sweep h2,
.sweep h3 {
	font-weight: 500;
	margin: 0;
}

.sweep h2 {
	font-size: 1.125rem;
	padding: .75rem 0 1.875rem;
	position: relative
}

.sweep h2 span {
	position: relative;
	display: inline-block
}

.sweep h3 {
	font-size: 1rem;
	text-align: left;
	padding: .5625rem 0 .75rem
}

.sweep .checkbox label {
	position: block;
	left: 0;
	top: 0;
	font-size: .75rem;
	padding-left: 2rem;
	padding-top: 0;
}

.sweep__checkbox {
	margin-bottom: rem(40);

	.error-message {
		display: none;
	}
}

.sweep-wrap {
	display: flex;
    padding: rem(50) 0;
}

.modal::before,
.modal__inner {
	display: inline-block;
	vertical-align: middle
}

.sweep-left {
	width: 56.63717%;
	padding-right: 5.625rem;
	position: relative;
	padding-top: rem(50);
}

.sweep-right {
	text-align: center
}

.sweep-icon {
	position: absolute;
	z-index: 3;
	font-size: 5px;
}

.sweep-icon--peace {
	left: 93%;
	top: 4.5%;
	transform: rotate(25deg);
}

.sweep-icon--sun {
	left: -11%;
	bottom: 9%;
	font-size: 4px;
	transform: rotate(-10deg);
}

.sweep-icon--stars {
	right: -14%;
	bottom: -7.2%
}

.sweep-img {
	position: relative;
	padding-bottom: 121.17647%;
	overflow: visible;
	max-width: 34.375rem;
	margin-left: auto
}

.sweep-img__top {
	z-index: 2;
	-webkit-transform: translate3d(-1.625rem, -1.625rem, 0);
	transform: translate3d(-1.625rem, -1.625rem, 0)
}

.sweep-img__bottom {
	z-index: 1
}

.sweep-img img {
	position: absolute;
	left: 0;
	top: 0
}

.sweep-check {
	text-align: left;
	padding: .625rem 0 1.875rem;
	position: relative
}

.modal,
.sweep-bottom {
	position: fixed;
	right: 0;
	bottom: 0;
	left: 0
}

.sweep-check .form-item {
	padding-bottom: 2.875rem;
}

.sweep-check__bottom {
	margin-bottom: 0;
	padding-bottom: .625rem!important
}

.sweep-check p {
	font-size: .75rem;
	line-height: 1.4;
	margin-bottom: 0
}

.sweep-check .input-error {
	margin-left: 2.25rem
}

.sweep-bottom {
	background-repeat: repeat-x;
	height: 2.8125rem;
	background-position: 50% 100%;
	border-top: 1px solid #ed3fa8;
	background-size: 1400px 32px;
	z-index: 3
}

.input-error {
	color: #e20000;
	text-align: left;
	font-size: .75rem;
	margin-top: .375rem
}

.sweep-thanks {
	background-color: #d7ece1;
	color: #2e8b57;
	padding: 1.25rem .625rem
}

.sweep-thanks p {
	margin: 0
}

.modal ul {
	font-size: .875rem;
	line-height: 1.6
}

.no-overflow {
	overflow: hidden
}

.modal {
	height: 100%;
	max-height: 100%;
	opacity: 0;
	overflow: auto;
	text-align: center;
	top: 0;
	width: 100%;
	z-index: 110;
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
	-webkit-transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
	transition: opacity .4s ease-in-out, visibility .4s ease-in-out
}

.modal__content,
.modal__inner {
	text-align: left
}

.modal::before {
	content: '';
	height: 100%;
	margin: 0 -.375rem 0 0;
	width: .0625rem
}

.modal.is-visible {
	opacity: 1;
	visibility: visible
}

.modal__inner {
	background-color: #fff;
	border: 1px solid #ed3fa8;
	max-width: 44.1875em;
	padding: 1.875rem;
	position: relative;
	width: 96%;
	z-index: 2;
	margin: .375rem 0
}

.modal__close {
	font-size: 1em;
	margin: 0;
	position: absolute;
	right: .875em;
	top: .875em;
	z-index: 2
}

.modal__content:after,
.modal__content:before {
	content: ' ';
	display: table
}

.modal__content p:last-of-type {
	margin-bottom: 0
}

.modal__header {
	margin-bottom: .625em;
	position: relative
}

.modal__header h3 {
	margin: 0;
	font-size: 1.375rem
}

.modal__note p {
	margin: 0;
	font-size: .6875em
}

.modal-overlay {
	background-color: #fff;
	bottom: 0;
	left: 0;
	opacity: 0;
	position: fixed;
	right: 0;
	top: 0;
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
	-webkit-transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
	transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
	visibility: hidden;
	width: 100%;
	z-index: 100
}

.modal-overlay.is-visible {
	opacity: .58;
	visibility: visible
}

